.search-box
    margin: 10px 0;
    border-top: 1px solid $ddd;
    border-bottom: 1px solid $ddd;
    
.address-list
    margin: 10px 0;
    border-bottom: 1px solid $ddd;

.list-item
    border-top: 1px solid $ddd;
    padding: 10px 15px;
    text-align: left;
    background: #fff;

.item-name
    font-size: 16px;
    color: $blank;
    line-height: 24px;

.item-address
    margin-top: 3px;
    font-size: 14px;
    color: $999;
    line-height: 18px;

.ellipsis
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

.list-title-box
    margin: 10px 0;
    display: flex;
    background: #fff;

.list-title-item
    flex: auto;
    text-align: center;
    line-height: 46px;
    border-top: 1px solid $ddd;
    border-bottom: 1px solid $ddd;

.list-title-box .list-title-item:nth-child(1)
    border-right: 1px solid $ddd;

.list-title-box .list-title-item.on
    color: $blue;

.clear-search-box
    margin: 10px 0;
    padding: 0 10px;
